.eventData {
  padding: 1vh 3vw;
  background: #fafafa;
  width: 100%;
  height: 100%;

  .eventData-content {
    background: #fff;

    .list {
      padding: 2vw 2vw 2vw;

      .title {
        position: relative;
        border-bottom: 1px solid #ccc;
        font-weight: 700;
        padding: 0 0 0.1rem 0.5rem;
        height: 1.5rem;
        overflow: hidden;

        .type {
          width: 4rem;
          line-height: 1.3rem;
          font-size: 0.8rem;
          color: #fff;
          background-color: rgb(255, 94, 0);
          display: block;
          text-align: center;
          transform: rotate(45deg);
          position: absolute;
          top: 0.1rem;
          right: -0.2rem;
        }
      }

      .item {
        display: flex;
        align-items: center;
        padding: 0.2rem;
        color: #959292;
        position: relative;

        img {
          width: 30px;
          height: 30px;
          vertical-align: text-bottom;
          margin-right: 10px;
        }

        span {
          padding-left: 1rem;
          color: #000;
        }

        .state {
          position: absolute;
          right: 1rem;
        }
      }
    }
  }
}